ip[data-v-c066c3c8] {\n        visibility: visible;\n        transition: opacity 0.3s ease-out 0.5s;\n        opacity: 1;\n}\n.remove-field[data-v-c066c3c8] {\n        display: none;\n        padding: 0 5px;\n        width: 5%;\n}\n.e-mail-addresses[data-v-c066c3c8] {\n        display: flex;\n        margin-bottom: 10px;\n}\n.e-mail-input:hover + .remove-field[data-v-c066c3c8],\n    .remove-field[data-v-c066c3c8]:hover {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        font-size: 24px;\n        cursor: pointer;\n}\n.multiple-address-info[data-v-c066c3c8] {\n        font-size: 14px;\n        line-height: 16px;\n        display: flex;\n        align-items: center;\n        padding: 12px;\n        margin: 20px 0 52px;\n        background: #F5F5FA;\n        border-radius: 4px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/organization/invitePeopleDialog.vue"],"names":[],"mappings":";AAqII;QACI,qBAAqB;AACzB;AACA;QACI,uBAAuB;AAC3B;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,kBAAkB;QAClB,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,yBAAyB;QACzB,UAAU;QACV,eAAe;QACf,cAAc;QACd,aAAa;AACjB;AACA;QACI;AACJ;AACA;QACI,aAAa;QACb,oBAAoB;AACxB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,kBAAkB;QAClB,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,iBAAiB;QACjB,kBAAkB;QAClB,iBAAiB;QACjB,YAAY;QACZ,uBAAuB;AAC3B;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,kBAAkB;QAClB,cAAc;QACd,mBAAmB;QACnB,aAAa;QACb,mBAAmB;QACnB,iBAAiB;QACjB,kBAAkB;QAClB,YAAY;AAChB;AACA;QACI;AACJ;AACA;QACI,kBAAkB;QAClB,kBAAkB;QAClB,mBAAmB;QACnB,cAAc;QACd,eAAe;QACf,qBAAqB;QACrB,kBAAkB;QAClB,kBAAkB;QAClB,qBAAqB;QACrB,wBAAwB;QACxB,SAAS;QACT,UAAU;QACV,UAAU;AACd;AACA;QACI,mBAAmB;QACnB,sCAAsC;QACtC,UAAU;AACd;AACA;QACI,aAAa;QACb,cAAc;QACd,SAAS;AACb;AACA;QACI,aAAa;QACb,mBAAmB;AACvB;AACA;;QAEI,aAAa;QACb,mBAAmB;QACnB,uBAAuB;QACvB,eAAe;QACf,eAAe;AACnB;AACA;QACI,eAAe;QACf,iBAAiB;QACjB,aAAa;QACb,mBAAmB;QACnB,aAAa;QACb,mBAAmB;QACnB,mBAAmB;QACnB,kBAAkB;AACtB","sourcesContent":["<template>\n    <div class=\"container\">\n        <div class=\"label-div\">\n            <label class=\"name-label\">Email Adresses</label>\n        </div>\n        <div style=\"overflow: auto; max-height: 470px;\">\n            <div v-for=\"(field, index) in invitations\" :key=\"index\" class=\"e-mail-addresses\">\n                <input class=\"e-mail-input\" :ref=\"'field-' + index\" v-model=\"field.mail\" @paste=\"handlePaste($event, index)\" size=\"50\" @input=\"addField(); field.mail = field.mail.toLowerCase()\"\n                    placeholder=\"name@example.com\" />\n                <span class=\"remove-field\" @click=\"removeField(index)\"><i class=\"mdi mdi-close\"></i></span>\n             </div>\n        </div>\n       \n        <div class=\"multiple-address-info\">\n            <i class=\"mdi mdi-information\" style=\"font-size: 24px; color: #151233; margin-right: 4px;\"></i>\n            <span>You can copy and paste multiple email addresses at once by using ';' between items.</span>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button id=\"sendButton\" class=\"send-invite-button\" :disabled=\"!checkEmailValidation() || checkDuplicateEmails()\" @click=\"inviteMember\">{{props.onBackoffice ? 'ADD OWNER' : 'SEND INVITATION'}}</button>\n            <span v-if=\"!checkEmailValidation()\" class=\"button-tooltip\" style=\"top: 340px; left: 470px; z-index: 1; width: 174px;\">Email addresses must be valid.</span>\n            <span v-if=\"checkDuplicateEmails()\" class=\"button-tooltip\" style=\"top: 340px; left: 470px; z-index: 1; width: 174px;\">There are duplicate mails.</span>\n        </div>\n    </div>\n</template>\n<script setup lang=\"ts\">\n    import { ref } from \"vue\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\n    import { IRouter } from \"../../../../../domain/presentation/IRouter\";\n    import { IInvitationRequestItem } from \"../../../../../../../common/qCloudTemp/symDtoObjects\";\n    \n    interface IInvitationProps{\n        ID?: string;\n        action?: 'add' | 'remove';\n        onBackoffice?: boolean;\n        inviteCb: (request: {invitations: Array<IInvitationRequestItem>, ID?: string, action?: 'add' | 'remove'}) => Promise<void>;\n    }\n\n    interface IInvitationItem {\n        mail: string;\n        name?: string;\n        id?: string;\n    }\n    const props = defineProps<IInvitationProps>();\n    type InvitationList = Array<IInvitationItem>;\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const router = container.resolve<IRouter>(IRouter);\n    const selectedOrganizationId = ref(viewModel.user?.selectedOrganization?.id);\n    const emit = defineEmits([\"close\"]);\n    const invitations = ref<InvitationList>([{ mail: \"\" }, { mail: \"\" },{ mail: \"\" }]);\n    const addField = (value?: IInvitationItem) => {\n        let emptyInputCount: number = 0;\n        invitations.value.forEach(element => {\n            if(element.mail == \"\") {\n                emptyInputCount++\n            }\n        });\n        if(emptyInputCount == 0) {\n            invitations.value.push(value || { mail: \"\" });\n        }\n    };\n    function close() {\n        emit(\"close\");\n    }\n    const removeField = (index: number) => {\n        if (invitations.value.length <= 1) { insertField(0, undefined, true); return false; }\n        invitations.value.splice(index, 1); return true;\n    };\n    const checkEmailValidation = () => {\n        let areAllEmailsValid = true;\n        let emptyMailCount = 0;\n        invitations.value.forEach(invite => {\n            if(invite.mail != '' && !validateMail(invite.mail)) {\n                areAllEmailsValid = false;\n                return areAllEmailsValid;\n            }\n        });\n        invitations.value.forEach(invite => {\n            if(invite.mail == '') {\n                emptyMailCount++;\n            }\n        })\n        if(emptyMailCount == invitations.value.length) {\n            areAllEmailsValid = false;\n        }\n        return areAllEmailsValid;\n    }\n    function checkDuplicateEmails () {\n        const emailAddresses = invitations.value.filter(item => item.mail != '').map(function(item){ return item.mail; })\n        const isDuplicate = emailAddresses.some(function(item, idx){ return emailAddresses.indexOf(item) != idx;} );\n        return isDuplicate;\n    }\n    const insertField = (index: number, value: IInvitationItem | undefined, override: boolean) => {\n        value = value || { mail: \"\" };\n        if (override) {\n            invitations.value.splice(index, 1, value);\n        } else {\n            invitations.value.splice(index, 0, value);\n        }\n    };\n    const handlePaste = (event: ClipboardEvent, index: number) => {\n        const { clipboardData } = event;\n        if (!clipboardData) { return; }\n\n        event.preventDefault();\n        const text = clipboardData.getData('text');\n        const values = text.split(/[\\s;]+/);\n\n        const override = invitations.value[index].mail == \"\";\n\n        insertField(index, { mail: values[0] }, override);\n        values.slice(1).forEach((value, curIndex) => insertField(index + curIndex + 1, { mail: value }, false));\n    }\n    function onManageOrganization() {\n        router.push(`/organization/manage/${selectedOrganizationId.value}`);\n    }\n    const inviteMember = async () => {\n        executor.execute(async () => {\n            const mails = invitations.value.map(inv => ({ email: inv.mail }));\n            props.inviteCb && await props.inviteCb({invitations: mails, ID: props.ID, action: props.action });\n            while (removeField(0));\n            close();\n            if(!props.onBackoffice) { onManageOrganization() };\n        }, { loading: true });\n    };\n    const validateMail = (email: string) => !email ? true : /^(?=.{1,64}@)[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+(\\.[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+)*@[a-zA-Z0-9-]+(\\.[a-zA-Z0-9-]+)*(\\.[a-zA-Z]{2,})$/.test(email); //RFC 5322-compliant 🤔\n    // /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|\"(?:[\\x01-\\x08\\x0b\\x0c\\x0e-\\x1f\\x21\\x23-\\x5b\\x5d-\\x7f]|\\\\[\\x01-\\x09\\x0b\\x0c\\x0e-\\x7f])*\")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\\x01-\\x08\\x0b\\x0c\\x0e-\\x1f\\x21-\\x5a\\x53-\\x7f]|\\\\[\\x01-\\x09\\x0b\\x0c\\x0e-\\x7f])+)\\])/.test(email); // 🤯 https://www.w3resource.com/javascript/form/email-validation.php // ne halt ettiğini anlamadım, isterseniz içinde 1 @ ve 1 . var mı diye de bakabiliriz. 😂\n\n</script>\n<style scoped>\n    * {\n        font-family: 'Roboto';\n    }\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .name-label {\n        font-weight: 500;\n        font-size: 15px;\n        line-height: 18px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .e-mail-input {\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        padding: 8px 8px 8px 12px;\n        width: 95%;\n        font-size: 15px;\n        color: #212121;\n        outline: none;\n    }\n    .e-mail-input::placeholder {\n        color: #BCBCBC\n    }   \n    .buttons-div {\n        display: flex;\n        justify-content: end;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .send-invite-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .send-invite-button:disabled {\n        color: #757575\n    }\n    .button-tooltip {\n        visibility: hidden;\n        position: absolute;\n        background: #757575;\n        color: #FFFFFF;\n        font-size: 11px;\n        font-family: 'Roboto';\n        text-align: center;\n        border-radius: 4px;\n        letter-spacing: 0.2px;\n        padding: 6px 8px 6px 8px;\n        top: 56px;\n        left: 63px;\n        opacity: 0;\n    }\n    .send-invite-button:disabled:hover + .button-tooltip {\n        visibility: visible;\n        transition: opacity 0.3s ease-out 0.5s;\n        opacity: 1;\n    } \n    .remove-field {\n        display: none;\n        padding: 0 5px;\n        width: 5%;\n    }\n    .e-mail-addresses {\n        display: flex;\n        margin-bottom: 10px;\n    }\n    .e-mail-input:hover + .remove-field,\n    .remove-field:hover {\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        font-size: 24px;\n        cursor: pointer;\n    }\n    .multiple-address-info {\n        font-size: 14px;\n        line-height: 16px;\n        display: flex;\n        align-items: center;\n        padding: 12px;\n        margin: 20px 0 52px;\n        background: #F5F5FA;\n        border-radius: 4px;\n    }\n</style>"],"sourceRoot":""}]);
